import React from 'react'

class Jumbotron extends React.Component {
    render(){
         return(    
            <div className="jumbotron jumbotron-fluid" title="Decorative Image Background.">
                <div className="container overlay">
                    <h1 className="display-4">{this.props.heading}</h1>
                    <p className="lead">{this.props.description}</p>
                    <hr className="my-4"/>
                    <p className="lead">
                        <a className="btn btn-primary btn-lg" href={this.props.src1} role="button">{this.props.cta1}</a>
                        
                    </p>   
                    <p className="lead">
                        <a className="btn btn-success btn-lg" href={this.props.src2} role="button">{this.props.cta2}</a>
                        
                    </p>   
                </div>
            </div>
            )
    }
}

export default Jumbotron